import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import './style.scss';

const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

export const Dashboard = () => {
	const classes = useStyles();

	return (
		<div className={classes.root}>
			<Grid container spacing={3}>
				<Grid item xs>
					<Paper className='dashboard-item'>
						<span className='title'>Dch v tr ph khc</span>
						<span className='number'>1000</span>
					</Paper>
				</Grid>
				<Grid item xs>
					<Paper className='dashboard-item'>
						<span className='title'>Ca hng</span>
						<span className='number'>1000</span>
					</Paper>
				</Grid>
				<Grid item xs>
					<Paper className='dashboard-item'>
						<span className='title'>Qung co</span>
						<span className='number'>1000</span>
					</Paper>
				</Grid>
			</Grid>
			<Grid container spacing={3}>
				<Grid item xs>
					<Paper className='dashboard-item'>
						<span className='title'>Gi tin</span>
						<span className='number'>1000</span>
					</Paper>
				</Grid>
				<Grid item xs>
					<Paper className='dashboard-item'>
						<span className='title'>Gi np tin</span>
						<span className='number'>1000</span>
					</Paper>
				</Grid>
				<Grid item xs>
					<Paper className='dashboard-item'>
						<span className='title'>Thnh vin</span>
						<span className='number'>1000</span>
					</Paper>
				</Grid>
			</Grid>
		</div>
	);
};
