import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import '../../../../pages/reset.scss';
import VisibilityIcon from '@material-ui/icons/Visibility';
import Button from '@material-ui/core/Button';
import './style.scss';

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		modal: {
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
		},
	}),
);

export const InfoCampaignList = () => {
	const classes = useStyles();
	const [open, setOpen] = React.useState(false);

	const handleOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};

	return (
		<div>
			<div onClick={handleOpen}>
				<VisibilityIcon />
			</div>
			<Modal
				aria-labelledby='transition-modal-title'
				aria-describedby='transition-modal-description'
				className={classes.modal}
				open={open}
				onClose={handleClose}
				closeAfterTransition
				BackdropComponent={Backdrop}
				BackdropProps={{
					timeout: 500,
				}}
			>
				<Fade in={open}>
					<div className=' form-modal informationStore'>
						<div className='form-modal__title'>
							<h3>CHI TIT CHIN DCH</h3>
						</div>
						<div className='form-modal__content'>
							<div className='CampaignList-code'>
								<span>M chin dch:</span>
								<span>
									<strong>CD_01</strong>
								</span>
							</div>
							<div className='form'>
								<div className='form_file'>
									<div className='InfoCampaignList__item'>
										<div className='content'>
											<div className='content__item'>
												<label htmlFor=''>Tn chin dch</label>
												<input className='form-input' placeholder='Thi trang cng s - duy nht hm nay gim gi 60%' disabled />
											</div>
											<div className='content__item'>
												<label htmlFor=''>Loi qung co</label>
												<input className='form-input' placeholder='Qung co c bn (v tr gia)' disabled />
											</div>
											<div className='content__item'>
												<label htmlFor=''>Khu vc hin th</label>
												<input className='form-input' placeholder='Ton quc' disabled />
											</div>
											<div className='content__item'>
												<label htmlFor=''>Danh mc</label>
												<input className='form-input' placeholder='Thi trang' disabled />
											</div>
											<div className='content__item'>
												<label htmlFor=''>Link qung co</label>
												<input className='form-input' placeholder='Thoitrangcongso.com' disabled />
											</div>
											<div className='content__item'>
												<label htmlFor=''>M t ngn</label>
												<textarea className='form-input' placeholder='Mang n cho ngi ph n v p sang trng, lch s, qu phi.' disabled />
											</div>
										</div>
									</div>
								</div>
								<div className='Time__CampaignList'>
									<div className='title'>
										<div className='Code'>
											<span>M giao dch:</span>
											<span>GD_01</span>
										</div>
										<div className='status'>
											<span>Hnh thc:</span>
											<span>Mi</span>
										</div>
									</div>
									<div className='content'>
										<div className='content_left'>
											<label htmlFor=''>M giao dch</label>
											<div className='time'>
												<span>GD_01</span>
												<span>GD_04</span>
											</div>
										</div>
										<div className='content_right'>
											<label htmlFor=''>Ngy thc hin</label>
											<div className='time'>
												<span>2/1/2021</span>
												<span>3/1/2021</span>
											</div>
										</div>
									</div>
								</div>
							</div>
							<div className='form_button'>
								<Button variant='outlined'>Kch hot</Button>
								<Button variant='outlined' type='submit' value='Submit'>
									Hy b
								</Button>
							</div>
						</div>
					</div>
				</Fade>
			</Modal>
		</div>
	);
};
