import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import VisibilityIcon from '@material-ui/icons/Visibility';
import '../../../../pages/reset.scss';
import ClearIcon from '@material-ui/icons/Clear';
import Button from '@material-ui/core/Button';
import '../../../../pages/reset.scss';
import './style.scss';
import { useSelector, useDispatch } from 'react-redux';
import { IApplicationState } from '../../../../store/reducers/state';
import { useEffect, useState } from 'react';
import { filterProduct } from '../../../../store/actions/store';
import { postfilterProduct } from '../../../../store/actions/store';
import { any } from 'prop-types';

const useStyles = makeStyles((theme: Theme) =>
	createStyles({
		modal: {
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
		},
	}),
);

export const InformationStore = () => {
	const classes = useStyles();
	const [open, setOpen] = React.useState(false);

	const handleOpen = () => {
		setOpen(true);
	};

	const handleClose = () => {
		setOpen(false);
	};
	const [filter, setFilter] = React.useState({
		price: [4000, 500000],
		size: ['S'],
	});
	const onSubmit = (event) => {
		event.preventDefault();
	};
	const addTags = (event) => {
		const value = event.target.value;
		const name = event.target.name;
		if (value !== '') {
			setFilter({ ...filter, [name]: value });
			event.target.value = '';
		}
		postTagFilter(filter);
	};
	const dispatch = useDispatch();
	const getfilterProduct = useSelector((state: IApplicationState) => {
		return state.filterProduct?.data;
	});
	useEffect(() => {
		dispatch(filterProduct());
	}, [dispatch]);

	const postTagFilter = (filter) => {
		dispatch(postfilterProduct(filter));
	};
	return (
		<div>
			<div onClick={handleOpen}>
				<VisibilityIcon />
			</div>
			<Modal
				aria-labelledby='transition-modal-title'
				aria-describedby='transition-modal-description'
				className={classes.modal}
				open={open}
				onClose={handleClose}
				closeAfterTransition
				BackdropComponent={Backdrop}
				BackdropProps={{
					timeout: 500,
				}}
			>
				<Fade in={open}>
					<div className=' form-modal informationStore'>
						<div className='form-modal__title'>
							<h3>Thng Tin C Bn</h3>
						</div>
						<div className='form-modal__content'>
							<div className='informationStore__item'>
								<label htmlFor=''>Gii thiu ngn</label>
								<div className='content'>
									<span>Chuyn cung cp cc dng sn phm thi trang hin i, sang trng,... ti phi p!</span>
								</div>
							</div>
							<div className='informationStore__item'>
								<label htmlFor=''>M t chi tit</label>
								<div className='content'>
									<span>
										T ho l thng hiu thi trang Vit Nam, ca hng khng ngng sng to trong vic tm kim, lm vic vi nhng i tc thi trang ln trn th gii cng vi
										pht huy sc mnh ni ti gip ca hng c c kinh nghim dy dn trong vic p ng dng sn phm cao cp (Hi-End) tiu chun quc t ti Vit Nam, ca ngi
										Vit Nam.
									</span>
								</div>
							</div>
							<form onSubmit={onSubmit}>
								<div className='informationStore__item'>
									<label htmlFor=''>Lin h</label>
									<div className='content'>
										<div className='content__item'>
											<label htmlFor=''>S in thoi</label>
											<input className='form-input' placeholder='nhp s in thoi'></input>
										</div>
										<div className='content__item'>
											<label htmlFor=''>Email</label>
											<input className='form-input' placeholder='nhp email'></input>
										</div>
										<div className='content__item'>
											<label htmlFor=''>Website</label>
											<input className='form-input' placeholder='nhp website'></input>
										</div>
										<div className='content__item'>
											<label htmlFor=''>a ch</label>
											<input className='form-input' placeholder='nhp a ch'></input>
										</div>
									</div>
								</div>
								<div className='informationStore__item'>
									<div className='content category__group'>
										<div className=''>
											<label htmlFor=''>Danh Mc Chnh</label>
											<select className='form-input'>
												<option>Thi trang</option>
												<option>m thc</option>
											</select>
										</div>
										<div className=''>
											<label htmlFor=''>Danh mc con</label>
											<select className='form-input'>
												<option>Qun o</option>
												<option> trang im</option>
											</select>
										</div>
									</div>
								</div>
								<div className='informationStore__item'>
									<label htmlFor=''>Thit lp b lc sn phm, dch v</label>
									<div className='content filter-group'>
										<div className='lable-group'>
											<span>Tn thuc tnh</span>
											<span>Gi tr</span>
										</div>
										<div className='filter-item'>
											<div className='filter-group--item'>
												<button className='form-input lable'>Gi</button>
												<div className=' text-content'>
													<input
														type='text'
														className='form-input'
														placeholder='nhp gi tr...'
														name='price'
														// onChange={handchange}
														// value={filter.price}
														onKeyUp={(event) => (event.key === 'Enter' ? addTags(event) : null)}
													/>
													<div className='form-button'>
														{getfilterProduct.map((item: any, index: number) => {
															console.log(item.price);
															// const price = item.price;
															// return price.map((item, index) => {
															// 	console.log(item);
															return (
																<button>
																	<span>
																		<ClearIcon />
																	</span>
																</button>
															);
															// });
														})}
													</div>
												</div>
											</div>
											<div className='filter-group--item'>
												<button className='form-input lable'>Kch thc</button>
												<div className=' text-content'>
													<input type='text' className='form-input' placeholder='nhp gi tr...' name='size' />
													<div className='form-button'>
														<button>
															<span>S</span>
															<span>
																<ClearIcon />
															</span>
														</button>
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
								<div className='form_button'>
									<Button variant='outlined'>Kch hot</Button>
									<Button variant='outlined' type='submit' value='Submit'>
										Hy b
									</Button>
								</div>
							</form>
						</div>
					</div>
				</Fade>
			</Modal>
		</div>
	);
};
