import React from 'react';
import { Instance } from '../../components/Dates';
import './style.scss';

export const FilterTime = () => {
	return (
		<div className='filter-overview'>
			<div className='filter-overview__item'>
				<label>Thi gian</label>
				<select className='form-input'>
					<option value={1}>30 ngy trc</option>
					<option value={2}>thng trc</option>
					<option value={2}>thng ny</option>
					<option value={2}>ty chn</option>
				</select>
			</div>
			<div className='filter-overview__item'>
				<label>Hnh thc</label>
				<select className='form-input'>
					<option value={1}>Dch V Tr Ph Khc</option>
					<option value={2}>Ca Hng</option>
					<option value={2}>Qung Co</option>
					<option value={2}>Gi Tin</option>
				</select>
			</div>
			<Instance />
		</div>
	);
};
