import React from 'react';
import Button from '@material-ui/core/Button';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import './style.scss';
import { useState } from 'react';
import MenuIcon from '@material-ui/icons/Menu';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

export const Sidebar = (props) => {
	const [value, setValue] = React.useState();

	const handleChange = (event, newValue) => {
		setValue(newValue);
	};

	return (
		<div className='sider_bar'>
			<div className='s-layout__sidebar'>
				<div className='s-sidebar__trigger'>
					<MenuIcon />
				</div>
				<nav className='s-sidebar__nav'>
					<div className='sider_bar__title'>
						<h2>TIMLARA</h2>
					</div>
					<div className='sider_bar__content'>
						<Tabs orientation='vertical' variant='scrollable' value={value} onChange={handleChange} aria-label='Vertical tabs example'>
							<Tab className='button-tab' label={<Link to='/'>Tng quan</Link>} />
							<Tab className='button-tab' label={<Link to='/package'>Gi tin</Link>} />
							<Tab className='button-tab' label={<Link to='/account-information'>Dch v</Link>} />
							<Tab className='button-tab' label={<Link to='/advertisement'>Chin dch</Link>} />
							<Tab className='button-tab' label={<Link to='/store'>Ca hng</Link>} />
							<Tab className='button-tab' label={<Link to='/account-information'>Tin ng</Link>} />
							<Tab className='button-tab' label={<Link to='/recharge-package'>Gi Np Tin</Link>} />
							<Tab className='button-tab' label={<Link to='/account'>Thnh vin</Link>} />
							<Tab className='button-tab' label={<Link to='/account-information'>Thng bo</Link>} />
							<Tab className='button-tab' label={<Link to='/category'>Danh mc</Link>} />
							<Tab className='button-tab' label={<Link to='/account-information'>Bo co thng k</Link>} />
							<Tab className='button-tab' label={<Link to='/configuration-management'>Cu Hnh</Link>} />
						</Tabs>
					</div>
				</nav>
			</div>
		</div>
	);
};
